<script>
import json from "../../../../ui/json/data.json";
import {mapMutations, mapGetters} from "vuex";
import OrderBook from "../../lib/contracts/orderBook";
export default {
  name: "BuySell",
  data() {
    return {
      myjson: json,
      showOrder: 1,
      card:1,
      eventDescription: this.$route.params.eventDescription,
      orderBook:  {
        buyBook: [
        ],
        sellBook: [
        ]
      },
      eventId: '0x14d7d13f868cdba323109afbbdbb69e5788af2bcf0b66b0d66b662bed4ffa62a',  
      price: null,
      quantity: null,
      total: null,
      orderTypes: [{value: 0, text:"Market"}, {value: 1, text: "Limit"}],
      orderTypeSelected: 1,
      BUY: 0,
      SELL: 1
    };
  },

  components: {},
  computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",  
        "loading",
        "balance",
        "durations",
        "selectedDuration"
    ]),
  },
  mounted: async function(){
        await this.init();
        this.allowance =  await this.client.getAllowance();
        await this.getOrdersForEvent();
    },
  methods: {
    async init(){
      this.client = new OrderBook({
                network: this.network,
                instrument: this.getSelectedInstrument,
                account: this.account,
            });
      await this.client.init();
      await this.getOrdersForEvent('');
    },
    async getOrdersForEvent(eventId){
      const orders = await this.client.getOrdersForEvent(this.eventId);
      this.orderBook.buyBook = [];
      this.orderBook.sellBook = [];
      for (let b=0; b<orders.buyBook.length; b++){
        this.orderBook.buyBook.push(orders.buyBook[b]);
      }
      for (let s=0; s<orders.sellBook.length; s++){
        this.orderBook.sellBook.push(orders.sellBook[s]);
      }
      console.log(this.orderBook)
        
    },
    async buySell(buySellDirection){
      if (this.quantity <= 0) {
        alert('Invalid Units'); return;
      }
      if (this.price <= 0.01 || this.price >= 0.99) {
        alert('Invalid Price'); return;
      }
      
      console.log('buySell', this.eventId, buySellDirection, this.orderTypeSelected, this.quantity, this.price);
      await this.client.placeOrderForEvent(this.eventId, buySellDirection, this.orderTypeSelected, this.quantity, this.price*100);
    },
    handleTotalChange() {
      this.total =
        this.price * this.quantity + 0.02 * (this.price * this.quantity);
        this.total = this.total.toFixed(2);
    },
    gotoEventList() {
      this.$router.push("/");
    },

    showMyOrders() {
      this.showOrder = 2;
    },

    showAllOrders() {
      this.showOrder = 1;
    },
     buy() {
      this.card = 1;
    },

    sell() {
      this.card = 2;
    },
    gotoOrderBook(eventDescription) {
      this.$router.push({
        name: "OrderBook",
        params: {
          eventDetail: eventDescription,
        },
      });
    },
  },
};
</script>

